"use client";
import { useState, useEffect, useRef } from "react";
import Image from "next/image";
import { assets } from "@/assets/assets";
import Sidebar from "./components/Sidebar";
import PromptBox from "./components/PromptBox";

export default function Home() {
  const [expand, setExpand] = useState(false);
  const [selectedChat, setSelectedChat] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const containerRef = useRef(null);

  useEffect(() => {
    if (containerRef.current) {
      containerRef.current.scrollTo({
        top: containerRef.current.scrollHeight,
        behavior: "smooth",
      });
    }
  }, [selectedChat?.messages]);

  return (
    <main className="flex h-screen w-full">
      <Sidebar
        expand={expand}
        setExpand={setExpand}
        selectedChat={selectedChat}
        setSelectedChat={setSelectedChat}
      />

      <div className="flex flex-col w-full">
        {/* Top Bar */}
        <div className="h-[60px] px-5 w-full border-b border-gray-700 flex items-center justify-between">
          <Image
            src={assets.menu_icon}
            alt="menu"
            className="w-[20px] h-[20px] cursor-pointer"
            onClick={() => setExpand((prev) => !prev)}
          />
          <p className="text-2xl font-medium">Hi, I am Astika's Chatbot App</p>
          <Image
            src={assets.user_icon}
            alt="user"
            className="w-[40px] h-[40px] rounded-full"
          />
        </div>

        {/* Messages Section */}
        <div
          ref={containerRef}
          className="flex-1 overflow-auto px-4 py-4 bg-[#1E1E1E]"
        >
          {!selectedChat?.messages || selectedChat.messages.length === 0 ? (
            <div className="text-gray-500 text-center mt-20">
              Start a new conversation
            </div>
          ) : (
            selectedChat.messages.map((message, index) => (
              <div
                key={index}
                className={`flex mb-4 ${
                  message.role === "user" ? "justify-end" : "justify-start"
                }`}
              >
                <div
                  className={`max-w-[75%] p-3 rounded-lg text-white ${
                    message.role === "user"
                      ? "bg-blue-600"
                      : "bg-gray-700"
                  }`}
                >
                  {message.content}
                </div>
              </div>
            ))
          )}
        </div>

        {/* PromptBox for user input */}
        <PromptBox
          selectedChat={selectedChat}
          setSelectedChat={setSelectedChat}
          isLoading={isLoading}
          setIsLoading={setIsLoading}
        />
      </div>
    </main>
  );
}
